@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.page-catalogue {
    @include adaptive-value(padding-bottom, 80, 20, 0);
    // .page-catalogue__title
    &__title {
        font-size: 36px;
        margin-bottom: 45px;
        line-height: 130%;
        color: #070c11;
        font-weight: 700;
        @media (max-width: 992px) {
            margin-bottom: 30px;
            font-size: 30px;
        }
        @media (max-width: 768px) {
            margin-bottom: 20px;
            font-size: 24px;
        }
    }
    // .page-catalogue__body
    &__body {
        display: flex;
        position: relative;
    }
    // .page-catalogue__filter-body
    &__filter-body {
        flex: 0 0 310px;
        margin-right: 15px;
        @media (max-width: 992px) {
            flex-basis: auto;
            max-width: 310px;
            width: 100%;
        }
        @media (max-width: 500px) {
            max-width: unset;
        }
    }
    // .page-catalogue__content-body
    &__content-body {
        flex: 1 1 975px;
        @media (min-width: 992px) {
            margin-top: -160px;
        }
    }
}
.filter-catalogue {
    @media (max-width: 992px) {
        background-color: #fff;
        padding: 0 20px 20px 20px;
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        z-index: 12;
        overflow-y: auto;
        transform: translateX(-150%);
        transition: all 0.5s;
        &.opened {
            transform: translateX(0);
        }
    }
    @media (max-width: 768px) {
        padding-bottom: 80px;
    }
    // .filter-catalogue__header
    &__header {
        display: none;
        align-items: center;
        padding: 20px;
        border-radius: 4px;
        background-color: #edf2f6;
        margin-bottom: 20px;
        @media (max-width: 992px) {
            display: flex;
            margin: 0 -20px 20px -20px;
        }
    }
    // .filter-catalogue__title
    &__title {
        flex: 1 1 auto;
        padding-right: 20px;
        padding-left: 34px;
        font-size: 20px;
        font-weight: 700;
        line-height: 130%;
        color: #070c11;
        background-image: url(../../assets/images/icons/filter.svg);
        background-repeat: no-repeat;
        background-position: left center;
        background-size: 24px;
    }
    // .filter-catalogue__btn
    &__btn {
        flex: 0 0 18px;
        height: 18px;
        background-color: transparent;
        background-image: url(../../assets/images/icons/close.svg);
        background-size: 18px;
        background-repeat: no-repeat;
        background-position: center;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-size: 20px;
            }
        }
    }
    // .filter-catalogue__form
    &__form {
    }
    // .filter-catalogue__item
    &__item {
        margin-bottom: 20px;
    }
}
.content-catalogue {
    // .content-catalogue__header
    &__header {
        display: flex;
        align-items: baseline;
        margin-bottom: 20px;
        @media (max-width: 992px) {
            justify-content: space-between;
        }
        @media (max-width: 500px) {
            flex-wrap: wrap;
        }
    }
    // .content-catalogue__products
    &__products {
    }
    // .content-catalogue__actions
    &__actions {
    }
}
.header-content-catalogue {
    @media (min-width: 992px) {
        min-height: 140px;
    }
    // .header-content-catalogue__btn
    &__btn {
        display: none;
        flex: 0 0 167px;
        padding: 16px 32px 16px 66px;
        background-color: #4878a6;
        border-radius: 4px;
        transition: all 0.3s;
        margin-right: 20px;
        span {
            color: #ffffff;
            font-weight: 500;
            display: inline-flex;
            position: relative;
            &::before {
                position: absolute;
                content: "";
                top: 50%;
                left: -32px;
                transform: translateY(-50%);
                width: 24px;
                height: 24px;
                background-image: url(../../assets/images/icons/filter-white.svg);
            }
        }
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
            }
        }
        @media (max-width: 992px) {
            display: flex;
        }
        @media (max-width: 500px) {
            flex: 1 1 100%;
            &:not(:last-child) {
                margin-bottom: 20px;
                margin-right: 0;
                justify-content: center;
                background-position: 32px center;
            }
        }
    }
    // .header-content-catalogue__body
    &__body {
        flex: 1 1 auto;
        padding-right: 20px;
        display: flex;
        align-items: baseline;
        &-exit-active {
            opacity: 0;
            transition: opacity 300ms ease-in;
        }
        @media (max-width: 1100px) {
            flex-wrap: wrap;
            padding-right: 0;
        }
        @media (max-width: 992px) {
            display: none;
        }
    }
    // .header-content-catalogue__select
    &__select {
        flex: 0 0 240px;
        z-index: 3;
        .select__input {
            padding: 5px 40px 5px 20px;
            font-size: 16px;
            line-height: 130%;
            color: #070c11;
            border: 1px solid #eaeaf0;
            border-radius: 4px;
            &::after {
                background-image: url(../../assets/images/icons/triangle-black.svg);
                background-position: center;
                width: 13px;
                height: 13px;
                transition: all 0.5s;
            }
        }
        .select__dropdown {
            border: 1px solid #eaeaf0;
            border-radius: 4px;
        }
        .select__item.selected {
            background-color: #dee9f1;
        }
        @media (max-width: 500px) {
            flex: 1 1 100%;
        }
    }
}

.products-content-catalogue {
    // .products-content-catalogue__row
    &__row {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    // .products-content-catalogue__all-cards
    &__all-cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-row-gap: 20px;
        @media (max-width: 1300px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 768px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }
    // .products-content-catalogue__no-products
    &__no-products {
        text-transform: uppercase;
        text-align: center;
        font-size: 24px;
        font-weight: 700;
    }
}
@keyframes spinner {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}
.user-catalogue-actions {
    text-align: center;
    // .user-catalogue-actions__more
    &__more {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 5px;
        width: 100%;
        background-color: #fff;
        padding: 20px;
        border: 1px solid #eaeaf0;
        border-radius: 0 0px 8px 8px;
        @media (any-hover: hover) {
            &:hover {
                .user-catalogue-actions__more-span {
                    color: #4878a6;
                }
                svg {
                    color: #4878a6;
                }
            }
        }
        &.loading {
            pointer-events: none;
            svg {
                animation: spinner 1s linear infinite;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: -2px;
        }
        &-span {
            transition: color 0.5s;
        }
        svg {
            color: #070c11;
            transition: all 0.5s;
        }
    }
    // .user-catalogue-actions__pagination
    &__pagination {
        .MuiPagination-root {
            margin-top: 50px;
            @media (max-width: 992px) {
                margin-top: 30px;
            }
            @media (max-width: 768px) {
                margin-top: 20px;
            }
        }
        .MuiPagination-ul {
            justify-content: center;
        }
        .MuiPaginationItem-root {
            min-width: 46px;
            height: 46px;
            color: #838688;
            font-family: "Exo20";
            font-size: 16px;
            border: 1px solid #eaeaf0;
            background-color: #fff;
            &.Mui-selected {
                background-color: #4878a6;
                color: #ffffff;
                pointer-events: none;
            }
            &.Mui-selected.Mui-focusVisible {
                background-color: #4878a6;
            }
            &.Mui-focusVisible {
                background-color: inherit;
                outline: 2px solid #2a5275;
            }
        }
    }
}
