@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 1;
    }
}

.filter-item {
    &.opened {
        .filter-item__body {
            padding: 20px 0;
            opacity: 1;
            visibility: visible;
            transform: scaleY(1);
            pointer-events: all;
        }
        .filter-item__header::after {
            transform: translateY(-50%) rotate(180deg);
        }
    }
    // .filter-item__header
    &__header {
        position: relative;
        padding: 10px 30px 10px 20px;
        color: #070c11;
        font-weight: 700;
        line-height: 130%;
        background-color: #edf2f6;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.5s;
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            right: 10px;
            transform: translateY(-50%);
            width: 13px;
            height: 9px;
            background-image: url(../../assets/images/icons/triangle-black.svg);
            background-repeat: no-repeat;
            background-position: center;
            transition: all 0.5s;
        }
        &.disabled {
            pointer-events: none;
            animation: pulse 1.5s ease-in-out 0.5s infinite;
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            transition: none;
        }
    }
    // .filter-item__body
    &__body {
        padding: 0;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        max-height: 0;
        transform: scaleY(0.5);
        transition: all 0.4s ease-in-out;
    }
    // .filter-item__characteristics-box
    &__characteristics-box {
        display: inline-flex;
        flex-direction: column;
    }
    // .filter-item__label
    &__label {
        display: inline-flex;
        align-items: center;
        position: relative;
        color: #070c11;
        white-space: nowrap;
        cursor: pointer;
        margin-bottom: 10px;
        &.checked::after {
            transform: scale(1);
        }
        span {
            padding-left: 3px;
            color: #838688;
        }
        &::before {
            content: "";
            flex: 0 0 20px;
            align-self: flex-start;
            height: 20px;
            border: 2px solid #eaeaf0;
            border-radius: 4px;
            margin-right: 10px;
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 20px;
            height: 20px;
            background: url(../../assets/images/icons/popup-small/checkbox-icon.svg) 4px 5px/13px 9px no-repeat,
                #4878a6 0 0/20px 20px;
            border-radius: 4px;
            transform: scale(0);
            transition: transform 0.3s;
        }
        &:focus-visible::before {
            outline: 2px solid #2a5275;
            outline-offset: 1px;
            border-radius: 2px;
            transition: none;
        }
    }
    // .filter-item__input
    &__input {
        display: none;
        &:checked + .filter-item__label::after {
            transform: scale(1);
        }
    }
    // .filter-item__btn
    &__btn {
        position: relative;
        z-index: 1;
        font-weight: 500;
        line-height: 130%;
        color: #ffffff;
        background-color: #4878a6;
        border-radius: 4px;
        width: 100%;
        padding: 16px 20px;
        transition: background-color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
            }
        }
        &:disabled {
            pointer-events: none;
            background-color: rgba(131, 134, 136, 0.5);
        }
        &:focus-visible {
            outline: 2px solid #000000;
            outline-offset: 2px;
            border-radius: 4px;
            transition: none;
        }
    }
}
