.header-content-catalogue {
    // .header-content-catalogue__list
    &__list {
        display: flex;
        flex-wrap: wrap;
        padding-right: 15px;
    }
    // .header-content-catalogue__btn-clear
    &__btn-clear {
        font-size: 14px;
        text-decoration: underline;
        color: #f15152;
        background-color: transparent;
        padding: 3px 5px;
        margin: 0 10px;
        opacity: 1;
        white-space: nowrap;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #b91717;
            }
        }
    }
}