@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 1;
    }
}
.product-skeleton {
    min-height: 600px;
    padding: 40px 20px 20px;
    border: 1px solid #eaeaf0;
    display: flex;
    flex-direction: column;
    position: relative;
    flex: 1 0 25%;
    @media (max-width: 1330px) {
        flex: 0 0 33.333%;
    }
    @media (max-width: 992px) {
        flex: 0 0 50%;
    }
    @media (max-width: 620px) {
        flex: 1 1 325px;
    }
    &__labels {
        position: absolute;
        content: "";
        top: 10px;
        right: 20px;
        z-index: 2;
        width: 90px;
        height: 16px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__img {
        height: 100%;
        max-height: 200px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        margin-bottom: 10px;
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__category {
        max-width: 40%;
        height: 20px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        margin-bottom: 10px;
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__title {
        height: 50px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        margin-bottom: 20px;
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__user-evaluation {
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 20px;
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__rating {
        flex: 0 1 50%;
        height: 20px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__reviews {
        flex: 0 1 30px;
        height: 20px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__prices {
        flex: 0 1 30%;
        height: 20px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__actions {
        flex: 0 1 90px;
        height: 46px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__purchase {
        display: flex;
        align-items: center;
        justify-content: space-between;
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__buy-btn {
        display: inline-flex;
        flex: 1 1 100%;
        height: 50px;
        border-radius: 4px;
        margin-right: 10px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
    &__cart-btn {
        flex: 0 0 50px;
        height: 50px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity 0.3s;
    }
}

.product .product-skeleton {
    border: none;
    &__img {
        height: 200px;
    }
}
