.select {
    position: relative;
    width: 100%;
    z-index: 5;
}
.select.opened .select__input {
    border-bottom: 1px solid transparent;
}
.select.opened .select__input::after {
    transform: translateY(-50%) rotate(180deg);
}
.select.opened .select__dropdown {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    transform: translateY(0) scaleY(1);
}
.select__input {
    border: 1px solid #ccc;
    height: 46px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 0 30px 0 15px;
    cursor: pointer;
    position: relative;
    background-color: #fff;
    &:focus-visible {
        outline: 2px solid #2a5275;
        border-radius: 5px;
        transition: none;
    }
}
.select__input::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 5px;
    width: 13px;
    height: 15px;
    background-image: url(./icon-down.svg);
    background-repeat: no-repeat;
    transition: all 0.3s;
}
.select__input span {
    pointer-events: none;
}
.select__search {
    width: 100%;
    padding: 10px 20px;
    border: 2px solid rgba(#2a5275, 0.6);
    &:focus-visible {
        outline: 1px solid #000000;
    }
}
.select__dropdown {
    position: absolute;
    top: 46px;
    left: 0;
    right: 0;
    border: 1px solid #ccc;
    max-height: 300px;
    overflow-y: auto;
    border-radius: 5px;
    box-shadow: 3px 4px 15px rgba(0, 0, 0, 0.3);
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateY(-50%) scaleY(0);
    transition: all .3s;
}
.select__list {
    padding-left: 0;
    margin-bottom: 0;
}
.select__item {
    padding: 0 15px;
    min-height: 46px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    &:focus-visible {
        outline: 2px solid #2a5275;
        outline-offset: -2px;
        border-radius: 1px;
        transition: none;
    }
}
.select__item:hover {
    background-color: #eee;
}
.select__item:not(:last-child) {
    border-bottom: 1px solid #ccc;
}
.select__item.selected {
    background-color: bisque;
}
.select__no-match {
    margin-bottom: 0;
    padding: 10px 15px;
    font-weight: 700;
}