@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.product {
    border: 1px solid #eaeaf0;
    @media (any-hover: hover) {
        &:hover {
            box-shadow: 0px 15px 20px rgba(42, 82, 117, 0.1);
            transition: all 0.3s;
        }
    }
    flex: 1 0 25%;
    @media (max-width: 1330px) {
        flex: 0 0 33.333%;
    }
    @media (max-width: 992px) {
        flex: 0 0 50%;
    }
    @media (max-width: 620px) {
        flex: 1 1 325px;
    }
    &-article {
        position: relative;
        min-height: 600px;
        padding: 40px 20px 20px;
        display: flex;
        flex-direction: column;
        @media (max-width: 768px) {
            min-height: unset;
        }
    }
    // .product__labels
    &__labels {
        position: absolute;
        content: "";
        top: 10px;
        right: 10px;
        z-index: 2;
        max-width: 160px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
    // .product__label
    &__label {
        flex: 0 1 auto;
        padding: 5px 15px;
        border-radius: 4px;
        color: #ffffff;
        font-weight: 500;
        text-align: right;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        // .product__label--new
        &--new {
            background-color: #48a69b;
        }
        // .product__label--top-sale
        &--top-sale {
            background-color: #d73838;
        }
    }
    // .product__link
    &__link {
        &:focus-visible {
            display: inline-block;
            outline: 2px solid #2a5275;
            border-radius: 1px;
            transition: none;
        }
        // .product__link--photo
        &--photo {
            text-align: center;
        }
        // .product__link--name
        &--name {
            &:focus-visible {
                outline: 2px solid #2a5275;
                border-radius: 1px;
                transition: none;
            }
        }
    }
    // .product__img
    &__img {
        width: 100%;
        max-height: 250px;
        max-width: 400px;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.05);
            }
        }
    }
    // .product__category
    &__category {
        font-weight: 500;
        line-height: 130%;
        color: #838688;
        margin-bottom: 10px;
    }
    // .product__title
    &__title {
        flex: 1 0 auto;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    // .product__name
    &__name {
        font-size: 20px;
        line-height: 130%;
        color: #070c11;
        @media (any-hover: hover) {
            &:hover {
                transition: all 0.3s;
                color: #4878a6;
                text-decoration: underline;
            }
        }
    }
    // .product__user-evaluation
    &__user-evaluation {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 20px;
    }
    // .product__rating
    &__rating {
        font-size: 32px;
        &:not(:last-child) {
            margin-right: 10px;
        }
        .rating__value {
            font-weight: 500;
        }
        @media (max-width: 400px) {
            font-size: 28px;
        }
    }
    // .product__reviews
    &__reviews {
        display: flex;
        align-items: center;
        column-gap: 3px;
        font-weight: 500;
        line-height: 100%;
        color: #070c11;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            border-radius: 1px;
            transition: none;
        }
        @media (max-width: 400px) {
            font-size: 14px;
        }
    }
    // .product__content
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        min-height: 80px;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    // .product__prices
    &__prices {
        align-self: center;
        margin-right: 10px;
    }
    // .product__old-price
    &__old-price {
        font-size: 16px;
        font-weight: 700;
        line-height: 130%;
        text-decoration: line-through;
        color: #838688;
        &::before {
            content: "$ ";
        }
    }
    // .product__price
    &__price {
        font-weight: 700;
        font-size: 24px;
        line-height: 130%;
        color: #070c11;
        margin-bottom: 5px;
        &::before {
            content: "$ ";
        }
    }
    // .product__discount
    &__discount {
        display: inline-block;
        background-color: #f37748;
        border-radius: 14px;
        padding: 3px 8px;
        max-width: 125px;
        font-size: 12px;
        font-weight: 700;
        color: #ffffff;
    }
    // .product__discount-percent
    &__discount-percent {
        display: inline-block;
        padding: 2px 4px;
        background-color: #fff;
        border-radius: 14px;
        color: #070c11;
        font-size: 14px;
        font-weight: 400;
    }
    // .product__actions
    &__actions {
    }
    // .product__btn
    &__btn {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #c8cacb;
        border-radius: 4px;
        width: 48px;
        height: 48px;
        background-color: transparent;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #dae1e6;
            }
        }
        &:not(:last-child) {
            margin-right: 5px;
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 4px;
            transition: none;
        }
        // .product__btn--favorite
        &--favorite {
            background: url(../../assets/images/icons/liked.svg) center no-repeat;
            .product-article.in-favorites & {
                background: url(../../assets/images/icons/products/favourite-added.svg) center no-repeat;
                @media (any-hover: hover) {
                    &:hover {
                        background: url(../../assets/images/icons/products/favourite-added.svg) center no-repeat,
                            #dae1e6;
                    }
                }
            }
        }
        // .product__btn--compare
        &--compare {
            background: url(../../assets/images/icons/compare.svg) center no-repeat;
            .product-article.in-compare & {
                background: url(../../assets/images/icons/products/compare-added.svg) center no-repeat;
                @media (any-hover: hover) {
                    &:hover {
                        background: url(../../assets/images/icons/products/compare-added.svg) center no-repeat, #dae1e6;
                    }
                }
            }
        }
    }
    // .product__purchase
    &__purchase {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    // .product__buy-btn
    &__buy-btn {
        display: inline-flex;
        flex: 1 1 100%;
        padding: 14px 15px;
        justify-content: center;
        font-weight: 700;
        line-height: 130%;
        color: #4878a6;
        white-space: nowrap;
        border: 1px solid #4878a6;
        border-radius: 4px;
        margin-right: 10px;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                border: 1px solid #4878a6;
                background-color: #2a5275;
                color: #ffffff;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 4px;
            transition: none;
        }
    }
    // .product__cart-btn
    &__cart-btn {
        flex: 0 0 50px;
        height: 51px;
        background: url(../../assets/images/icons/products/cart-white.svg) center no-repeat, #4878a6;
        border-radius: 4px;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 3px;
            border-radius: 4px;
            transition: none;
        }
        .product-article.in-cart & {
            background: url(../../assets/images/icons/products/cart-added.svg) center no-repeat, #edf2f6;
            @media (any-hover: hover) {
                &:hover {
                    background: url(../../assets/images/icons/products/cart-added.svg) center no-repeat, #dae1e6;
                }
            }
        }
    }
}
.rating {
    display: flex;
    align-items: center;

    // .rating__value
    &__value {
        font-size: 50%;
        line-height: 1;
        padding-left: 10px;
    }
}
