// MUI slider (price in catalogue)
.price-slider {
    // .price-slider__body
    &__body {
    }
    // .price-slider__inputs
    &__inputs {
        display: flex;
        column-gap: 20px;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    // .price-slider__item
    &__item {
        display: flex;
        align-items: center;
    }
    // .price-slider__label
    &__label {
        color: #838688;
        padding-right: 10px;
    }
    // .price-slider__input
    &__input {
        padding: 14px 20px;
        border: 1px solid #eaeaf0;
        border-radius: 4px;
        width: 100%;
        max-width: 115px;
        color: #838688;
        @media (max-width: 400px) {
            padding: 14px;
        }
        // .price-slider__input--min
        &--min {
        }
        // .price-slider__input--max
        &--max {
        }
    }
    // .price-slider__slider
    &__slider {
        padding: 0 10px;
        .MuiSlider-root {
            height: 6px;
            border-radius: 12px;
            color: #4878a6;
        }
        .MuiSlider-rail {
            background-color: #eaeaf0;
            opacity: 1;
        }
    }
}
