.header-content-catalogue {
    // .header-content-catalogue__item
    &__item {
        display: inline-flex;
        align-items: center;
        column-gap: 7px;
        padding: 5px 10px;
        background-color: #edf2f6;
        border-radius: 102px;
        margin-top: 3px;
        margin-bottom: 3px;
        margin: 3px;
        span {
            font-size: 14px;
            padding-right: 3px;
        }
        &-enter {
            opacity: 0;
            transform: scale(0);
        }
        &-enter-active {
            opacity: 1;
            transform: scale(1);
            transition: opacity 300ms ease-in, transform 200ms ease-in;
        }
        &-exit {
            opacity: 1;
            transform: scale(1);
        }
        &-exit-active {
            opacity: 0;
            transform: scale(0);
            transition: all 300ms ease-in, transform 200ms ease-in;
        }
        // .header-content-catalogue__item-btn
        &-btn {
            svg {
                color: #F15152;
                width: 10px;
                height: 10px;
                transition: all .3s;
            }
            @media (any-hover: hover) {
                &:hover {
                    svg {
                        color:#b91717;
                    }
                }
            }
        }
    }
}